// figure img
figure {
    width: 100%;
    height: auto;
    padding: 0;
    margin: 0;

    img {
        width: 100%;
        max-width: 100%;
        height: auto;
        padding: 0;
        margin: 0;
        object-fit: contain;
    }
}

%button {
    display: inline-block;
    text-align: center;
    // padding: calc(8px + 0.1vw) calc(16px + 0.5vw);
    padding: .5rem 1rem;
    color: white;
    // border-radius: calc(3px + 0.1vw);
    border-radius: $cards__border-radius;
    cursor: pointer;
    // border: 1px solid rgba(0,0,0,.3);
    border: none;
    letter-spacing: .1rem;
    text-decoration: none;
    outline: none;
    font-weight: 300;
    // box-shadow: 0px 2px 5px 2px rgba(0,0,0,.1);
}

%button-outline {
    display: inline-block;
    text-align: center;
    // padding: 8px 16px;
    padding: px2rem(8px) px2rem(16px);
    // padding: calc(8px + 0.1vw) calc(16px + 0.5vw);
    // border-radius: calc(3px + 0.1vw);
    // border-radius: 3px;
    border-radius: px2rem(3px);
    cursor: pointer;
    // border: 1px solid rgba(0,0,0,.4);
    border: px2rem(1px) solid rgba(0,0,0,.4);
    font-weight: 300;
    // opacity: .6;
    // border: none;
    // letter-spacing: 1px;
    text-decoration: none;
}

%row {
    @include is-grid-container('minmax(3vw, 1fr) minmax(auto, 1200px) minmax(3vw, 1fr)');
    width: 100%;
}